import React, { useState, useEffect } from "react";
import Person from "./components/Person";
import Notification from "./components/Notification";
import phonebookService from "./services/persons";

const App = () => {
  const [phonebook, setPhonebook] = useState([]);
  const [newPerson, setNewPerson] = useState("");
  const [newPhoneNumber, setPhoneNumber] = useState("");
  const [errorMessage, setErrorMessage] = useState(null);

  useEffect(() => {
    phonebookService.getAll().then((res) => {
      setPhonebook(res);
    });
  }, []);

  const addPerson = (event) => {
    event.preventDefault();
    const noteObject = {
      name: newPerson,
      number: newPhoneNumber,
    };
    console.log(noteObject);

    const putName = phonebook.find((person) => person.name === noteObject.name);
    const checkIfNameExists = phonebook.some((person) => person.name === noteObject.name);
    console.log('logg', checkIfNameExists);
    if (checkIfNameExists) {
      let id = putName.id
      console.log(id);
      phonebookService
        .update(id ,noteObject)
        .then(() => {
          phonebookService.getAll().then((res) => {
            setPhonebook(res);
          });
          setNewPerson("");
          setPhoneNumber("");
        })
        .catch((err) => {
          console.log(err);
          setErrorMessage(err.message);
          setTimeout(() => {
            setErrorMessage(null);
          }, 5000);
        });
    } else {
      phonebookService
        .create(noteObject)
        .then((returnedNote) => {
          setPhonebook(phonebook.concat(returnedNote));
          setNewPerson("");
          setPhoneNumber("");
        })
        .catch((err) => {
          console.log(err);
          setErrorMessage(err.message);
          setTimeout(() => {
            setErrorMessage(null);
          }, 5000);
        });
    }
  };

  const handlePersonChange = (event) => {
    setNewPerson(event.target.value);
  };

  const handlePhoneNumberChange = (event) => {
    setPhoneNumber(event.target.value);
  };

  const handleDelete = (person) => {
    phonebookService
      .deletePerson(person)
      .then((returnedNote) => setPhonebook(returnedNote));
  };

  return (
    <div>
      <h1>Persons</h1>
      <Notification message={errorMessage} />
      <ul>
        {phonebook.map((person, i) => (
          <Person key={i} person={person} handleDelete={handleDelete} />
        ))}
      </ul>
      <form onSubmit={addPerson}>
        <input
          value={newPerson}
          placeholder={"Type new name..."}
          onChange={handlePersonChange}
          required
        />
        <input
          value={newPhoneNumber}
          placeholder={"Type new phoneNumber..."}
          onChange={handlePhoneNumberChange}
          required
        />
        <button type="submit">save</button>
      </form>
    </div>
  );
};

export default App;
